    // legendGroups.append("text")
    //   .attr("x", 20)
    //   .attr("y", 9)
    //   .text(d => d);
    // 创建图例
    const legend = svg.selectAll(".legend")
        .data(color.domain())
        .enter().append("g")
        .attr("class", "legend")
        .attr("transform", (d, i) => `translate(200, ${i * 20})`); // 将图例整体右移 100px


    // 添加图例标记（颜色示例）
    legend.append("rect")
        .attr("x", width - 18)
        .attr("width", 18)
        .attr("height", 18)
        .style("fill", color);

    // 添加图例标签
    legend.append("text")
        .attr("x", width - 24)
        .attr("y", 9)
        .attr("dy", ".35em")
        .style("text-anchor", "end")
        .text(d => d);
